"use client";

import { useState, useEffect } from "react";
import {
  Box,
  Grid,
  Button,
  TextField,
  MenuItem,
  IconButton,
  InputAdornment,
  Select,
  FormControl,
  InputLabel,
  Popover,
  Typography,
   OutlinedInput,autocompleteClasses,Autocomplete

} from "@mui/material";

import { ArrowRightAlt, SwapHoriz, Search, People } from "@mui/icons-material";
import { LocalizationProvider, DatePicker } from "@mui/x-date-pickers";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import TravellersDropdown from "./TravellersDropDown";



// Example countriesAirports array
const countriesAirports = [
  { city: "Dubai", airport: "Dubai International Airport", code: "DXB" },
  { city: "Abu Dhabi", airport: "Abu Dhabi Intl Airport", code: "AUH" },
  { city: "Karachi", airport: "Jinnah International Airport", code: "KHI" },
  { city: "Lahore", airport: "Allama Iqbal Airport", code: "LHE" },
  { city: "Delhi", airport: "Indira Gandhi International Airport", code: "DEL" },
  { city: "Mumbai", airport: "Chhatrapati Shivaji Maharaj Airport", code: "BOM" },
  { city: "Riyadh", airport: "King Khalid Intl Airport", code: "RUH" },
  { city: "Jeddah", airport: "King Abdulaziz Intl Airport", code: "JED" },
  { city: "Berlin", airport: "Berlin Brandenburg Airport", code: "BER" },
  { city: "Frankfurt", airport: "Frankfurt Airport", code: "FRA" },
];


export default function FlightSearchForm() {
  // ---------------- STATES ----------------
  const [flyingFrom, setFlyingFrom] = useState("");
  const [destinationTo, setDestinationTo] = useState("");

  const [selectedTripType, setSelectedTripType] = useState("one-way");
  const [selectedCabinClass, setSelectedCabinClass] = useState("economy");

  const [travellers, setTravellers] = useState({
    adults: 1,
    children: 0,
    infants: 0
  });

  const [departDate, setDepartDate] = useState(null);
  const [returnDate, setReturnDate] = useState(null);

  // Initial dates
  useEffect(() => {
    const today = new Date();
    const plus7 = new Date();
    plus7.setDate(today.getDate() + 7);
    setDepartDate(today);
    setReturnDate(plus7);
  }, []);

  const swap = () => {
    const temp = flyingFrom;
    setFlyingFrom(destinationTo);
    setDestinationTo(temp);
  };

  const tripTypeOptions = [
    { value: "one-way", label: "One Way" },
    { value: "return", label: "Return" }
  ];

  const cabinClassOptions = [
    { value: "economy", label: "Economy" },
    { value: "premium-economy", label: "Premium Economy" },
    { value: "business", label: "Business" },
    { value: "first-class", label: "First Class" }
  ];

  // ----------------- JSX -----------------
  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <Box sx={{ p: 2, maxWidth: 900, mx: "auto" }}>
        {/* Trip type + cabin */}
        <Grid container spacing={2} mb={2} >
          <Grid item xs={6} sm={3}>
             <FormControl fullWidth size="small">
             
              <Select
                value={selectedTripType}
                onChange={(e) => setSelectedTripType(e.target.value)}
                  sx={{fontSize:"12px"}}
              >
                {tripTypeOptions.map((option) => (
                  <MenuItem key={option.value} value={option.value}
                      sx={{fontSize:"10px", }}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>

          <Grid item xs={6} sm={3}>
            <FormControl fullWidth size="small">
             
              <Select
                value={selectedCabinClass}
                onChange={(e) => setSelectedCabinClass(e.target.value)}
              sx={{fontSize:"12px"}}
              >
                {cabinClassOptions.map((option) => (
                  <MenuItem key={option.value} value={option.value}
                  sx={{fontSize:"8px"}}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>

        {/* Main Grid */}
      <Grid container spacing={1} alignItems="center">
          {/* Flying From */}
     <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
  {/* Flying From */}
  <Autocomplete
    options={countriesAirports}
    getOptionLabel={(option) => `${option.city} (${option.code})`}
    value={countriesAirports.find(a => a.code === flyingFrom) || null}
    onChange={(event, newValue) => setFlyingFrom(newValue ? newValue.code : "")}
    sx={{
      width: 160,
      "& .MuiAutocomplete-option": {
        fontSize: "8px",   //  ⭐ dropdown list font size
      }
    }}
    renderInput={(params) => (
      <TextField
        {...params}
        label="Flying From"
        size="small"
        sx={{
          "& .MuiInputBase-root": { height: 30, fontSize: "12px" },
          "& .MuiInputLabel-root": { fontSize: "12px", top: "-1px" },
        }}
      />
    )}
  />

  {/* Swap Button */}
  <IconButton
    onClick={() => {
      const temp = flyingFrom;
      setFlyingFrom(destinationTo);
      setDestinationTo(temp);
    }}
    size="small"
    sx={{
      mx: -0.5,
      bgcolor: "#3366ff",
      color: "white",
      "&:hover": { bgcolor: "#00BFFF" },
    }}
  >
    <SwapHoriz />
  </IconButton>

  {/* Destination To */}
  <Autocomplete
    options={countriesAirports}
    getOptionLabel={(option) => `${option.city} (${option.code})`}
    value={countriesAirports.find(a => a.code === destinationTo) || null}
    onChange={(event, newValue) => setDestinationTo(newValue ? newValue.code : "")}
    sx={{
      width: 160,
      "& .MuiAutocomplete-option": {
        fontSize: "12px",   //  ⭐ dropdown list font size
      }
    }}
    renderInput={(params) => (
      <TextField
        {...params}
        label="Destination To"
        size="small"
        sx={{
          "& .MuiInputBase-root": { height: 30, fontSize: "12px" },
          "& .MuiInputLabel-root": { fontSize: "12px", top: "-1px" },
        }}
      />
    )}
  />
</Box>


          {/* Dates */}
<Box sx={{ display: "flex", gap: 1, width: 230 }}>
  
  {/* Depart Date */}
  <FormControl fullWidth variant="outlined" size="small">
    <InputLabel shrink sx={{ fontSize: "10px" }}>Depart Date</InputLabel>
    <OutlinedInput
      type="date"
      value={departDate}
      onChange={(e) => setDepartDate(e.target.value)}
      inputProps={{ style: { height: 30, padding: "0 8px", fontSize: "12px" }}}
      label="Depart Date"
    />
  </FormControl>

  {/* Return Date (only on Return trip) */}
  {selectedTripType === "return" && (
    <FormControl fullWidth variant="outlined" size="small">
      <InputLabel shrink sx={{ fontSize: "10px" }}>Return Date</InputLabel>
      <OutlinedInput
        type="date"
        value={returnDate}
        onChange={(e) => setReturnDate(e.target.value)}
        inputProps={{ style: { height: 30, padding: "0 8px", fontSize: "12px" }}}
        label="Return Date"
      />
    </FormControl>
  )}

</Box>


          {/* Travellers */}
        <Grid item xs={12} sm={2}>
  <TravellersDropdown
    travellers={travellers}
    onTravellersChange={setTravellers}
    size="small"
    sx={{ 
      "& .MuiButton-root": { fontSize: "8px" },
      "& .MuiTypography-root": { fontSize: "8px" }
    }}
  />
</Grid>


          {/* Search */}
          <Grid item xs={12} sm={1}>
            <Button
              variant="contained"
              color="primary"  
              startIcon={<Search  sx={{height:30, pl:"12px", py:"5px"}}/>}
              sx={{width:"60px", alignItems:"center", height:30,margin:"auto" }}  // adjust button size
            >
          
            </Button>
          </Grid>
        </Grid>
      </Box>
    </LocalizationProvider>
  );
}
