"use client";

import { useState, useEffect } from "react";
import { TextField, Grid, IconButton, InputAdornment, MenuItem, Box } from "@mui/material";
import { MapPin, Search } from "lucide-react";
import TravellersDropdown from "./TravellersDropDown";

// Sample countriesAirports data (replace with your full array)
const countriesAirports = [
  {
    country: "UAE",
    cities: [
      { city: "Dubai", airport: "Dubai International Airport", code: "DXB" },
      { city: "Abu Dhabi", airport: "Abu Dhabi Intl Airport", code: "AUH" },
    ],
  },
  {
    country: "Pakistan",
    cities: [
      { city: "Karachi", airport: "Jinnah International Airport", code: "KHI" },
      { city: "Lahore", airport: "Allama Iqbal Airport", code: "LHE" },
    ],
  },
  {
    country: "India",
    cities: [
      { city: "Delhi", airport: "Indira Gandhi International Airport", code: "DEL" },
      { city: "Mumbai", airport: "Chhatrapati Shivaji Maharaj Airport", code: "BOM" },
    ],
  },
];

const HotelSearchForm = () => {
  const [citySearch, setCitySearch] = useState("");
  const [selectedCity, setSelectedCity] = useState(null);
  const [travellers, setTravellers] = useState({ adults: 2, children: 0, infants: 0 });
  const [checkInDate, setCheckInDate] = useState("");
  const [checkOutDate, setCheckOutDate] = useState("");

  // Set initial dates
  useEffect(() => {
    const today = new Date();
    const nextWeek = new Date();
    nextWeek.setDate(today.getDate() + 7);

    setCheckInDate(today.toISOString().split("T")[0]);
    setCheckOutDate(nextWeek.toISOString().split("T")[0]);
  }, []);

  // Flatten all cities
  const allCities = countriesAirports.flatMap((country) =>
    country.cities.map((city) => ({
      ...city,
      country: country.country,
    }))
  );

  // Filter cities by search
  const filteredCities = allCities.filter(
    (c) =>
      c.city.toLowerCase().includes(citySearch.toLowerCase()) ||
      c.country.toLowerCase().includes(citySearch.toLowerCase())
  );

  return (
    <Grid container spacing={2} alignItems="center" margin={2} paddingTop={2} paddingBottom={2}>
      {/* City */}
   <Grid item xs={12} sm={4}>
  <TextField
  label="Search by city"
  size="small"
  sx={{
    "& .MuiInputBase-root": { height: 30, fontSize: "12px" },
    width: 280,
    "& .MuiInputLabel-root": { fontSize: "12px", top: "-1px" },
  }}
  value={selectedCity ? `${selectedCity.city}, ${selectedCity.country}` : ""}
  onChange={(e) => setCitySearch(e.target.value)}
 
  
  select={filteredCities.length > 0}
>
  {filteredCities.map((city, index) => (
    <MenuItem
      key={`${city.city}-${city.country}-${index}`}
      value={`${city.city}, ${city.country}`} // must match TextField value
      onClick={() => {
        setSelectedCity(city);  // set selected city
        setCitySearch(`${city.city}, ${city.country}`); // optional: keep search updated
      }}
    >
      {city.city}, {city.country} ({city.code})
    </MenuItem>
  ))}
</TextField>

</Grid>


      {/* Check-In */}
      <Box  sx={{ display: "flex",justifyContent:"space-around", gap: 1 }}>
      <Grid item xs={12} sm={2}>
        <TextField
          label="Check-In"
          type="date"
          size="small"
          fullWidth
          value={checkInDate}
          onChange={(e) => setCheckInDate(e.target.value)}
 inputProps={{ style: { height: 30, width:"160px", padding: "0 8px", fontSize: "12px" }}}
        />
      </Grid>

      {/* Check-Out */}
      <Grid item xs={12} sm={2}>
        <TextField
          label="Check-Out"
          type="date"
          size="small"
          fullWidth
          value={checkOutDate}
          onChange={(e) => setCheckOutDate(e.target.value)}
         inputProps={{ style: { height: 30, width:"160px", padding: "0 8px", fontSize: "12px" }}}
        />
      </Grid>
      </Box>

      {/* Travellers */}
      <Grid item xs={12} sm={3}>
        <TravellersDropdown travellers={travellers} onTravellersChange={setTravellers} />
      </Grid>

      {/* Search Button */}
   <Grid item xs={12} sm={2}>
  <IconButton
    color="primary"
    size="small"
    sx={{
      backgroundColor: "#1976d2",
      "&:hover": { backgroundColor: "#115293" },
      width: "100%",
      height: "100%",
      borderRadius: 1, // removed border radius
    }}
  >
    <Search style={{ color: "white" }} />
  </IconButton>
</Grid>

    </Grid>
  );
};

export default HotelSearchForm;
