"use client";

import { useState } from "react";
import {
  Popover,
  IconButton,
  Typography,
  Box,
  Button
} from "@mui/material";
import { People, Add, Remove } from "@mui/icons-material";

export default function TravellersDropdown({ travellers, onTravellersChange }) {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const open = Boolean(anchorEl);

  const handleTravellerChange = (type, operation) => {
    const newValue = operation === "increment" ? travellers[type] + 1 : travellers[type] - 1;

    if (type === "adults" && newValue >= 1 && newValue <= 9) {
      onTravellersChange({ ...travellers, [type]: newValue });
    } else if (type === "children" && newValue >= 0 && newValue <= 8) {
      onTravellersChange({ ...travellers, [type]: newValue });
    } else if (type === "infants" && newValue >= 0 && newValue <= 5 && newValue <= travellers.adults) {
      onTravellersChange({ ...travellers, [type]: newValue });
    }
  };

  const totalTravellers = travellers.adults + travellers.children + travellers.infants;
  const displayText = totalTravellers > 0 ? `${totalTravellers} Traveller${totalTravellers > 1 ? "s" : ""}` : "Travellers";

  return (
    <>
      <Button
        variant="outlined"
        size="small"
        fullWidth
        onClick={handleOpen}
        startIcon={<People sx={{ color: "black", fontSize: "12px" }} />}
        sx={{
          color: "black",
          borderColor: "black",
          fontSize: "12px",
          "&:hover": { borderColor: "black", backgroundColor: "#f5f5f5" }
        }}
      >
        {displayText}
      </Button>

      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
      >
        <Box sx={{ p: 2, width: 200 }}>
          <Typography variant="subtitle2" mb={1} sx={{ fontSize: "12px" }}>Travellers</Typography>

          {["adults", "children", "infants"].map((type) => {
            const label = type.charAt(0).toUpperCase() + type.slice(1);
            const subLabel = type === "adults" ? "12+ years" : type === "children" ? "2-11 years" : "Under 2 years";
            const value = travellers[type];
            const max = type === "adults" ? 9 : type === "children" ? 8 : 5;
            const disableDecrement = type === "adults" ? value <= 1 : value <= 0;
            const disableIncrement = type === "infants" ? value >= travellers.adults || value >= max : value >= max;

            return (
              <Box key={type} display="flex" justifyContent="space-between" alignItems="center" mb={1}>
                <Box>
                  <Typography variant="body2" sx={{ fontSize: "12px" }}>{label}</Typography>
                  <Typography variant="caption" color="text.secondary" sx={{ fontSize: "10px" }}>{subLabel}</Typography>
                </Box>
                <Box display="flex" alignItems="center">
                  <IconButton
                    size="small"
                    disabled={disableDecrement}
                    onClick={() => handleTravellerChange(type, "decrement")}
                    sx={{ color: "black" }}
                  >
                    <Remove fontSize="small" />
                  </IconButton>
                  <Typography sx={{ mx: 1, fontSize: "12px" }}>{value}</Typography>
                  <IconButton
                    size="small"
                    disabled={disableIncrement}
                    onClick={() => handleTravellerChange(type, "increment")}
                    sx={{ color: "black" }}
                  >
                    <Add fontSize="small" />
                  </IconButton>
                </Box>
              </Box>
            );
          })}
        </Box>
      </Popover>
    </>
  );
}
